import react, {useEffect, useRef, useState} from "react";
import firebase from "../firebase";
import {connect} from "react-redux";
import {updateMessages, updateUsersCounter} from "../redux/store";
import s from "./Messages.module.css";
import Message from "./Message";
import Loader from "./loader/Loader";

export const db = firebase.firestore();
const Messages = (props) => {
    const [isLoader, setIsLoader] = useState(true);
    const [usersOnline, setUsersOnline] = useState(0);
    useEffect( () => {
      console.log("что-то поменялось" + usersOnline);
    }, [usersOnline] )

    useEffect( () => {
       //  узнаю сколько там лежит
       db.collection("usersOnline").doc("counter").get().then(doc => {
          //увеличиваю на +1 юзера  
          db.collection("usersOnline").doc("counter").set({
            count: doc.data().count + 1
          })
       })
       
      // получаю кол-во человек онлайн
      const unsub = db.collection("usersOnline").onSnapshot(
        function(snapshot) {
          // snapshot.forEach( doc => props.updateUsersCounter(doc.data().count) )
          snapshot.forEach( doc => {
            console.log("на сервере" + doc.data().count);
            setUsersOnline(doc.data().count)
          } )
       })
     
      // получаю смс
      const unsubscribe = db.collection("messages").orderBy("date", "asc").limitToLast(15).onSnapshot(
       function(snapshot) {
         let messages = [];
         snapshot.forEach( doc => messages.push({ userId: doc.data().userId, message: doc.data().message, userEmail: doc.data().userEmail, mesId: doc.data().date.seconds}) )
         props.updateMessages( messages );
         setIsLoader(false);
      });

      return () => {
        db.collection("usersOnline").doc("counter").get().then(doc => {
          db.collection("usersOnline").doc("counter").set({
            count: doc.data().count - 1
          })
       })
        unsubscribe();
        unsub();
      }
    }, [])

    let messagesDiv = useRef(null);
    if (messagesDiv.current) {
      setTimeout(() => {
        messagesDiv.current.scrollTop = messagesDiv.current.scrollHeight;
      }, 1);
    }
    return(
       <div className={s.messages} ref={messagesDiv}>
           {isLoader 
           ? <Loader /> 
           : props.messages.map( (m) => 
           <Message 
           key={m.mesId} 
           userId={m.userId} 
           message = {m.message}
           currentUserId= {props.id} 
           userEmail = {m.userEmail}/> )
           }
           <div className={s.counter}>Онлайн: {usersOnline} </div> 
       </div>
    )
}
let mapStateToProps = (state) => {
    return{
      messages: state.messages,
      id: state.currentUser.uid,
      usersOnline: state.usersOnline,
    }
}
export default connect(mapStateToProps, {updateMessages, updateUsersCounter})(Messages);